import axios, { Axios, CreateAxiosDefaults } from 'axios'
import { GalxeSpacePoints } from './galxe-client.model'
import { GALXE_GRAPHQL_ENDPOINT } from './galxe-client.const'
import { get } from 'lodash'

export class GalxeClient {
  // Axios instance
  private axios: Axios

  constructor(token: string) {
    this.axios = this.initAxios(token)
  }

  /**
   * Axios initialization
   */
  private initAxios(token: string) {
    const options: CreateAxiosDefaults = {
      baseURL: GALXE_GRAPHQL_ENDPOINT,
      headers: {
        'x-api-key': token,
        'content-type': 'application/json',
      },
    }
    return axios.create(options)
  }

  /**
   * Get campaign data by wallet address
   * @param campaignId - the campaign ID
   * @param walletAddress - the wallet address
   * @returns the wallet data
   */
  public getCampaignDataByWallet(
    campaignId: number,
    walletAddress: string
  ): Promise<GalxeSpacePoints | undefined> {
    // Prepare query
    const query = `
      query SpaceLoyaltyPoints(
        $id: Int,
        $address: String!,
        $sprintId: Int) {
        space(id: $id) {
          id
          addressLoyaltyPoints(address: $address, sprintId: $sprintId) {
            id
            points
            rank
          }
        }
      }
    `

    const variables = {
      id: campaignId,
      address: walletAddress,
    }

    return this.axios
      .request({
        method: 'POST',
        data: {
          query,
          variables,
        },
      })
      .then((response) => get(response.data, 'data.space'))
  }
}
